.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  appearance: none;
  border: 1px solid transparent;
  border-radius: 0;
  font-size: 15px;
  font-weight: 500;
  font-family: inherit;
  line-height: 1;
  cursor: pointer;
  background-color: #00b1e2;
  color: #fff;
  text-decoration: none;
  padding: 16px 24px;
  transition: background-color 0.25s ease-in-out, color 0.25s ease-in-out;
}
@media (hover: hover) {
  .btn:hover {
    background-color: #181a1c;
    color: #fff;
  }
}
.btn--outline {
  background-color: inherit;
  border-color: #181a1c;
}
.btn--white {
  background-color: #fff;
  color: #181a1c;
}
.btn--outline-white {
  background-color: inherit;
  border-color: #fff;
}
.btn--dark {
  background-color: #181a1c;
  color: #fff;
}
@media (hover: hover) {
  .btn--dark:hover {
    background-color: #00b1e2;
    color: #fff;
  }
}

.link {
  text-decoration: none;
  color: #00b1e2;
  display: inline-block;
  transition: color 0.25s ease-in-out, border-color 0.25s ease-in-out;
}
@media (hover: hover) {
  .link:hover:hover {
    color: #2874b2;
  }
}
.link:active {
  color: #5b5f63;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

