.header-menu-toggle {
  position: relative;
  border: none;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 64px;
  z-index: 10;
  transition: background-color 0.25s ease-in-out;
}
@media (min-width: 1200px) {
  .header-menu-toggle {
    display: none;
  }
}
@media (hover: hover) {
  .header-menu-toggle:hover {
    background-color: #f0f8fa;
  }
  .header-menu-toggle:hover .header-menu-toggle__line {
    background-color: #00b1e2;
  }
}
.header-menu-toggle__line {
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #fff;
  border-radius: 2px;
  transition: transform 0.25s ease-in-out, background-color 0.25s ease-in-out, opacity 0.25s ease-in-out;
}
.header-menu-toggle__line:first-child {
  top: 50%;
  transform: translateY(-5px);
  transition: 0.25s ease-in-out;
}
.header-menu-toggle__line:last-child {
  top: 50%;
  transform: translateY(5px);
  transition: 0.25s ease-in-out;
}
.header-menu-toggle--active {
  background-color: #f0f8fa;
}
.header-menu-toggle--active .header-menu-toggle__line:first-child {
  transform: translateY(0) rotate(45deg);
  transform-origin: center;
  background-color: #00b1e2;
}
.header-menu-toggle--active .header-menu-toggle__line:last-child {
  transform: translateY(0) rotate(-45deg);
  transform-origin: center;
  background-color: #00b1e2;
}

